import React, {useState,useEffect} from 'react'
import './Stores.scss';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import {actions as inviteActions} from '../../../redux/ducks/invite_app';
import Loader from '../../../utils/Loader/Loader';
import { notify } from '../../../Input-controls/Toast/Toast';
import { CheckIndianNumber } from '../../../utils/Validator';
import { Clevertap, Hobspot } from '../../../utils/Utils';
import { LazyImage } from '../../../utils/LazyImage';

function Stores(props) {

  const [mobVal,setMobVal] = useState("");
  const [buttonClick,setButtonClick] = useState(false);


  const changeMobFn = (e) =>{
    
    let lval = e.target.value;
    if(!isNaN(parseInt(lval))){
      if(lval.length < 11){
        setMobVal(parseInt(lval));
      }
    }
    else{
      setMobVal("");
    }
  }
  const sendInviteFn = (e) => {
    if(!mobVal){
      notify.warning("Please enter mobile number");
      Clevertap('GetTheApp',{clicked:true, mobile:"invalid mobile"});
    }
    else if(CheckIndianNumber(mobVal)){
      Clevertap('GetTheApp',{clicked:true, mobile:mobVal});
      setButtonClick(true);

      Hobspot.push({
          "phone_number": mobVal,
      })

      props.inviteApp(mobVal);
    }else{
      notify.danger("Please check the number you have entered")
    }
  }

  useEffect(()=>{
    if(buttonClick && props.inviteAppProps.isLoading === false){
      setButtonClick(true);
      // eslint-disable-next-line eqeqeq
      if(!!props.inviteAppProps.data && props.inviteAppProps.data.statusCode == 1)
      {
        setMobVal("")
        notify.success(props.inviteAppProps.data.msg);
      }
      else{
        notify.danger("Failed. Please try after some time.");
      }
    }
    // eslint-disable-next-line
  },[props.inviteAppProps])

    return (
      <>
      {props.inviteAppProps.isLoading?<Loader/>:
        <>
        <div style={{marginTop: '40px'}}>
            <div className="pickeddiv">
                <div className="storesflex">
                    <div id = "OneAppIdHome" className="shippedtext mainHeading_max">The one app for all your healthcare needs</div>
                    <div className="icons">
                        <div className="Appstore">
                            <a href="https://apps.apple.com/in/app/bits-pilani/id1510755114" rel="noopener noreferrer" target="_blank" >
                                <LazyImage src="https://res.cloudinary.com/bits-pilani/image/upload/fl_lossy,f_auto,q_auto/v1594198884/New-home-screen/HomeAppStrIcns/AppStrIcn_pgul6w.svg" alt="social_media" className="applogo" />
                            </a>
                        </div>
                        <div className="Playstore">
                            <a href="https://play.google.com/store/apps/details?id=com.bits-pilani" rel="noopener noreferrer" target="_blank">
                                <LazyImage src="https://res.cloudinary.com/bits-pilani/image/upload/fl_lossy,f_auto,q_auto/v1594198884/New-home-screen/HomeAppStrIcns/playStrIcn_lwfat2.svg" alt="social_media" className="playlogo" />
                            </a>
                        </div>
                    </div>
                    <div className="fulldiv">
                    <div className="numberflex">
                       <div className="flagflexx">
                           <LazyImage src="https://res.cloudinary.com/bits-pilani/image/upload/fl_lossy,f_auto,q_auto/v1594104912/New-home-screen/image_51_1_gmlaz9.svg" alt="flag" className="flagimg" />
                       </div>
                       <div className="linediv-clss"></div>
                       <div className="secondflex">

                           <input type="number"
                            placeholder="Mobile Number"
                            name="phone"
                            className="digit"
                            label=""
                            onChange={(e)=>changeMobFn(e)}
                            value={mobVal} />
                       </div>
                       <div className="arrowflex" onClick={(e)=>sendInviteFn()}>
                              <LazyImage src="https://res.cloudinary.com/bits-pilani/image/upload/fl_lossy,f_auto,q_auto/v1590071460/New-home-screen/Arrow_1_2x_eht8k5.png" alt="Arrow" className="rightarrow" />
                       </div>
                   </div>
                    </div>
                </div>
                <div className="imageflex">
                    <div>
                    <LazyImage src="https://res.cloudinary.com/bits-pilani/image/upload/v1597052598/New-home-screen/getTheApp_ztxdvo.svg" alt="phone" className="onimg" />
                    </div>

                </div>
            </div>

        </div>
        </>}
        </>
    )
}

const mapStateToProps = (state) => {
  return {
    inviteAppProps:state.invite_app
  }
}

  const mapDispatchToProps = (dispatch) => ({
    ...bindActionCreators({
        ...inviteActions,
    }, dispatch)
  });


  export default connect(mapStateToProps, mapDispatchToProps)(Stores)
